import React from 'react'
import {Movies} from '../components/Movies'
import {Search} from '../components/Search'
import {Preloader} from '../components/Preloader';




class Main extends React.Component {
    state = {
        movies: [],

        loading: true,
     
    };

//    ,  response,     json.   data(  )
// this.setState({movies: data.Search}))  setState   movies  data      Search
// 78584b3c&s -    omdbapi.com
    componentDidMount() {
        fetch('https://www.omdbapi.com/?apikey=78584b3c&s=matrix')
            .then((response) => response.json())
            .then((data) => this.setState({ movies: data.Search, loading: false })
           )
        // / 
            .catch((err) => {
                console.error(err);
                this.setState({ loading: false })
            })
    }
    

//    state,   str( ),        type   'all'
// this.setState({movies: data.Search}))  setState   movies  data      Search
    searchMovies = (str, type = 'all') => {
//      ,      false 
        this.setState({loading: true})
        //   type   'all',     get -  type}
        fetch(`https://www.omdbapi.com/?apikey=78584b3c&s=${str}${type !== 'all' ? `&type=${type}` : ''}`)
             .then(response => response.json())
             .then(data => this.setState({movies: data.Search, loading: false })
        )
            // / 
            .catch((err) => {
                console.error(err);
                this.setState({ loading: false });
            });
    };

    render() {
        //
        const { movies, loading } = this.state;

        return (
            <main className="container content">
               <Search searchMovies={this.searchMovies} />
{/*      Preloader,       */}
               {loading ?  <Preloader /> :  <Movies movies={movies} />}  
            </main>
        );
    }
}


export { Main };


