import React from 'react'
import {Movie} from './Movie'


//     
function Movies (props) {
    const { movies = [] } = props;
//       imdbID
    return (
           <div className='movies'>
            {/*      ,    */}
               {movies.length ? (
                   movies.map((movie) => <Movie key={movie.imdbID} {...movie} />)
               ) : (
            //     Nothing found<
                   <h4>Nothing found</h4>
        )}
    </div>
    );
}

export {Movies};


